.marquee {
  margin-top: 10%;
  height: 90px;
  background-color: white;
  justify-content: space-between;
}

.marquee--inner {
  width: 263%;
  position: absolute;
  padding: 5px;
  animation: marquee 25s linear infinite;
}

.marquee--inner:hover {
  animation-play-state: paused;
}

.marquee--span {
  float: left;
  width: 50%;
  justify-content: space-around;
}

@keyframes marquee {
  0% { right: 0; }
  100% { right: -100%; }
}

