.navbar {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 4vw;
  background-color: #fff;
  position: relative;
  padding: 0px 70px;
  background-color: #fff;
}
.navbar-logo { 
  width: 83%;
  margin-left: 30px;
}
.navbar-links {
  list-style-type: none;
  display: flex;
  margin-top: -4px;
  margin-left: -30px;
}
.navbar-links li a {
  display: block;
  text-decoration: none;
  color: #444;
  padding: 20px 27px;
  font-weight: 500;
}
.navbar-links li.navbar-dropdown {
  position: relative;
  padding-top: 0px;
}
.navbar-links li.navbar-dropdown:hover .dropdown {
  visibility: visible;
  opacity: 1;
  transform: translateY(0px);
}
.navbar-links li.navbar-dropdown .dropdown {
  visibility: hidden;
  font-size: 12px;
  opacity: 0;
  position: absolute;
  top: 100%;
  transform: translateY(50px);
  left: 0;
  width: 120px;
  background-color: #fff;
  box-shadow: 0px 10px 10px 3px rgba(0, 0, 0, 0.3);
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  z-index: 111;
  transition: 0.4s all;
}
.navbar-links li.navbar-dropdown .dropdown a {
  padding-top: 8px;
  padding-bottom: 8px;
  font-weight: 400;
}
.navbar-dropdown .dropdown a:hover {
  padding-left: 30px;
}
.navbar-links li a:hover {
  color: #4DB8EF;
}
.nav-bttn{
  background-color: #0FE8Ff;
  border: none;
  text-align: center;
  height: 45px;
  border-radius: 5px;
  width: 105px;
  color: white;
  margin-top: 3.5%;
  margin-left: -30px;
  box-shadow: 1px 1px 5px 1px #8080808f;
  -webkit-animation: bounce .5s infinite alternate;
  -moz-animation: bounce .5s infinite alternate;
  animation: bounce .5s infinite alternate;
}
@-webkit-keyframes bounce {
  to { -webkit-transform: scale(1.1); }
}
@-moz-keyframes bounce {
  to { -moz-transform: scale(1.1); }
}
@keyframes bounce {
  to { transform: scale(1.1); }
}

